import Vue from 'vue'
import Router from 'vue-router'

import aaaaa from '@/components/mine/pages/AAAA'//


// ------------------------------------------register1
import login from '@/components/login/login' // 
import register from '@/components/login/register' // 
import register1 from '@/components/login/register1' // 

// ------------------------------------------
import index from '@/components/index/index'// search

import order from '@/components/dingdan/dingdan'// 
import orderInfo from '@/components/dingdan/orderInfo'// 
import wancheng from '@/components/dingdan/wancheng'// 
import yichangdingdan from '@/components/dingdan/yichangdingdan'// 

import pinche from '@/components/pinche/index'// 
import pincheList from '@/components/pinche/pincheList'// 
import pincheInfo from '@/components/pinche/pincheInfo'// 
import yuyuebaojia from '@/components/pinche/yuyuebaojia'// 
import shangmen from '@/components/pinche/shangmen'// 


// ------------------------------------------
import mypage from '@/components/mine/mine'//
import infos from '@/components/mssage/index'//
import infoss from '@/components/mssage/mssageInfo'//
import invitationCode from '@/components/mine/pages/yaoqingma'//
import myoney from '@/components/mine/pages/myoney'//
import mingxi from '@/components/mine/pages/mingxi'//
import tixian from '@/components/mine/pages/tixian'//
import shimingrenzheng from '@/components/mine/pages/shimingrenzheng'//
import opinion from '@/components/mine/pages/yijian'//
import ourselves from '@/components/mine/pages/guanyuwomen'//
import myContract from '@/components/mine/pages/wodehets'//
import wodehetong from '@/components/mine/pages/wodehetong'//
import jiedanzhong from '@/components/mine/pages/jiedanzhong'//
import baojia from '@/components/mine/pages/baojia'//
import tianjiabaojia from '@/components/mine/pages/tianjiabaojia'//
import myBankard from '@/components/mine/pages/myBankard'//
import bangdingka from '@/components/mine/pages/bangdingka'//
import share from '@/components/mine/pages/share'//

// ------------------------------------------
import demo from '@/components/demo/index'//demo


Vue.use(Router);

export default new Router({
    routes:
        [{
            path: '/',
            redirect: '/index'
        }, {
            path: '/login',
            name: 'login',
            component: login
        }, {
            path: '/register',
            name: 'register',
            component: register
        }, {
            path: '/register1',
            name: 'register1',
            component: register1
        }, {
            path: '/order',
            name: 'order',
            component: order
        }, {
            path: '/orderInfo',
            name: 'orderInfo',
            component: orderInfo
        }, {
            path: '/yichangdingdan',
            name: 'yichangdingdan',
            component: yichangdingdan
        }, {
            path: '/shangmen',
            name: 'shangmen',
            component: shangmen
        }, {
            path: '/wancheng',
            name: 'wancheng',
            component: wancheng
        }, {
            path: '/pinche',
            name: 'pinche',
            component: pinche
        }, {
            path: '/pincheList',
            name: 'pincheList',
            component: pincheList
        }, {
            path: '/pincheInfo',
            name: 'pincheInfo',
            component: pincheInfo
        }, {
            path: '/yuyuebaojia',
            name: 'yuyuebaojia',
            component: yuyuebaojia
        }, {
            path: '/demo',
            name: 'demo',
            component: demo
        }, {
            path: '/invitationCode',
            name: 'invitationCode',
            component: invitationCode
        }, {
            path: '/myoney',
            name: 'myoney',
            component: myoney
        }, {
            path: '/mingxi',
            name: 'mingxi',
            component: mingxi
        }, {
            path: '/tixian',
            name: 'tixian',
            component: tixian
        }, {
            path: '/index',
            name: 'index',
            component: index
        }, {
            path: '/infos',
            name: 'infos',
            component: infos
        }, {
            path: '/infoss',
            name: 'infoss',
            component: infoss
        }, {
            path: '/ID',
            name: 'shimingrenzheng',
            component: shimingrenzheng
        }, {
            path: '/opinion',
            name: 'opinion',
            component: opinion
        }, {
            path: '/mypage',
            name: 'mypage',
            component: mypage,
        }, {
            path: '/ourselves',
            name: 'ourselves',
            component: ourselves,
        }, {
            path: '/myContract',
            name: 'myContract',
            component: myContract,
        }, {
            path: '/wodehetong',
            name: 'wodehetong',
            component: wodehetong,
        }, {
            path: '/jiedanzhong',
            name: 'jiedanzhong',
            component: jiedanzhong,
        }, {
            path: '/mybaojia',
            name: 'mybaojia',
            component: baojia,
        }, {
            path: '/aaaaa',
            name: 'aaaaa',
            component: aaaaa,
        }, {
            path: '/tianjiabaojia',
            name: 'tianjiabaojia',
            component: tianjiabaojia
        }, {
            path: '/myBankard',
            name: 'myBankard',
            component: myBankard
        }, {
            path: '/bangdingka',
            name: 'bangdingka',
            component: bangdingka
        }, {
            path: '/share',
            name: 'share',
            component: share
        }]
})



